@use 'base/variables'as v;

.sidebar {
  background-color: v.$indigo;
  padding: 2rem;

  h2 {
    color: v.$blanco;
    margin: 0;
    text-align: center;
  }

  .sidebar-nav {
    margin-top: 5rem;
    a{
      color: v.$blanco;
      display: block;
      margin-bottom: 1.2rem;
      padding: .7rem;
      transition: all 0.3s ease-in-out;
      text-transform: uppercase;
      font-weight: bold;
      font-size: 1.3rem;
      &:hover,
      &.activo{
        cursor: pointer;
        background-color: darken(v.$indigo,30%);
        border-radius: .5rem;
      }
    }
  }
}