@use 'base/variables' as v;
@use 'base/mixins' as m;
@media (prefers-color-scheme: dark){
  $grisOscuro : #2c2c3b;
  body{
    background-color: $grisOscuro;

  }
  .sidebar,.barra{
    background-color: #1d1c27;
  }
  .descripcion-pagina,.login .acciones a,
  .crear .acciones a,
  .olvide .acciones a{
    color: #ccc;
  }
  .tagline, .formulario, label{
    color: v.$blanco ;
  }
  
  .barra p{
    color : #ccc;
  }
  .dashboard, .barra{
    background-color: $grisOscuro;
  }
  .nombre-pagina{
    color: v.$blanco;
  }

  .filtros{
    background-color:lighten($grisOscuro,30%);
    h2,
    label{
      color: v.$blanco;
    }

  }
  .filtros .filtros-inputs input[type=radio]{
    accent-color: v.$naranja;
  }
  #listado-tareas .tarea{
 
    background-color: lighten($grisOscuro,10%) ;
    border: none ;
    p{
      color: #ccc;
    }
  }
  .tarea{
    padding: 1rem;
  }

  // modal
  .modal .formulario.animar{
    background-color: $grisOscuro;
  }
}