@use 'base/variables' as v;
@use 'base/mixins' as m;
.no-proyectos{
  text-align: center;
  padding: 1rem;
  font-weight: 700;
  text-transform: uppercase;
  a{
    color : v.$indigo;
  }
}
.listado-proyectos{
  list-style: none;
  padding: 0;
  margin-top:3rem; 
  @include m.grid(1,2rem);
  @include m.tablet{
    @include m.grid(3,2rem);
  }
  @include m.desktop{
    @include m.grid(3,2rem);
  }
  // @include m.grid(3,2rem);
  .proyecto{
    background-color: lighten(v.$indigo,5%);
    min-height: 12rem;
    display: flex;
    align-items: center;
    border-radius: .5rem;
    padding: 2rem;
    line-height: 2;
    transition: all 0.5s ease-in-out;
    &:hover{
      background-color: darken(v.$indigo, 5%);
      cursor:pointer;
      transform: scale(1.08);
    }
    a{
      
      color:v.$blanco;
    }
  }
}