@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-nueva-tarea{
  display: flex;
  justify-content: center;
  .boton{
    border-radius: .5rem;
    @include m.boton(v.$cyan, v.$blanco);
  }
}

#listado-tareas{
  list-style: none;
  padding: 0;
  margin-top: 5rem;
  .no-tareas{
    text-align: center;
  }
}

.tarea{
  padding: .5rem .2rem;
  border-bottom: 2px solid darken(v.$gris, 5%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;

  &:last-of-type{
    border: none;
  }
  p{
    font-size: 2.2rem;
  }
  .opciones{
    display: flex;
    gap:1rem;

    button{
      border: none;
      font-size: 1.2rem;
      text-transform: uppercase;
      padding: .5rem 1rem;
      font-weight: 700;
      border-radius: 1rem;

      &:hover{
        cursor: pointer;
      }
     
    }
    .completa{
      background-color: v.$azul;
      color: v.$blanco;
    }
    .pendiente{
      background-color: v.$naranja;
      color: v.$blanco;
    }
    .eliminar-tarea{
      background-color: v.$rojo;
      color: v.$blanco;
    }
  }
}