@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
  background-color: #f3f4f6;

  @include m.tablet{
    display: flex;
    height: 100vh;
  }

  .principal{
    flex:1;
    overflow: scroll;

    .contenido{
      padding: 2rem;
    
      .nombre-pagina{
        text-align: center;
      }
    }
  }
  .formulario{
    input:not([type="submit"]){
      background-color: v.$blanco;
    }
    input[type="submit"]{
      @include m.boton(v.$indigo, v.$blanco);
      border-radius: .5rem;
    }
  }
}
.enlace_password{
  color: v.$indigo;
  &:hover{
    color: darken(v.$indigo, 20%);
  }
}